.course-center-details {
  padding-top: 60px;
  width: 100%;
  background: #F6F7FB;
  min-width: 1366px;
}
.course-center-details .operation-pic .ant-card {
  border-radius: 5px;
  padding-bottom: 20px;
  min-height: auto!important;
}
.course-center-details .operation-pic .ant-card .ant-card-body {
  padding: 0;
}
.course-center-details .operation-pic .center-link {
  display: block;
  width: 100%;
  background: none;
  border: none;
  padding-bottom: 10px;
}
.course-center-details .operation-pic .center-link .button {
  padding: 6px 10px;
  height: 28px;
  background: #3BB9FF;
  border-radius: 3.1856px;
  font-family: Alibaba PuHuiTi;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 120.7%;
  color: #FFFFFF;
}
.course-center-details .operation-pic .center-link .button img {
  width: 8px;
  margin-right: 5px;
  margin-top: -2px;
}
.course-center-details .operation-pic .center-top {
  min-height: auto!important;
  background: #F6F7FB;
  border: none;
  height: auto!important;
}
.course-center-details .operation-pic .center-top .center-title {
  margin: 30px 0 26px 0;
}
.course-center-details .operation-pic .center-top .center-title .center-title-img {
  margin-right: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 134px;
  height: 86px;
  background: url(../../../../assets/images/placeholder_bg.png) no-repeat;
}
.course-center-details .operation-pic .center-top .center-title .center-title-img img {
  width: 100%;
  height: 100%;
}
.course-center-details .operation-pic .center-top .center-title .center-title-img > span {
  padding: 2px 6px;
  position: absolute;
  left: 10px;
  bottom: 10px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 2px;
  color: #fff;
  font-size: 12px;
}
.course-center-details .operation-pic .center-top .center-title .center-title-img > span .course-logo-l {
  width: 12px !important;
  height: 12px !important;
  vertical-align: -1px;
  margin-right: 5px;
  display: inline-block;
  min-width: auto;
  min-height: auto;
}
.course-center-details .operation-pic .center-top .center-title .title1 {
  font-size: 20.6874px;
  line-height: 120.7%;
  color: #2A283D;
  margin-bottom: 10px;
}
.course-center-details .operation-pic .center-top .center-title .title2 {
  font-size: 15.7618px;
  color: #2A283D;
  opacity: 0.4;
  margin-bottom: 10px;
}
.course-center-details .operation-pic .center-top .center-title .title2 span {
  margin-right: 20px;
}
.course-center-details .operation-pic .center-top .center-title .title3 {
  margin-bottom: 10px;
}
.course-center-details .operation-pic .center-top .center-title .title3 .span1 {
  font-size: 12px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.4;
}
.course-center-details .operation-pic .center-top .center-title .title3 .span2 {
  font-size: 12px;
  color: #2A283D;
  opacity: 0.4;
  margin-left: 15px;
}
.course-center-details .operation-pic .center-top .center-title .title3 .Progress-content {
  width: 300px;
  margin-left: 20px;
}
.course-center-details .operation-pic .center-top .center-title .title3 .Progress-content .ant-progress-inner {
  background: #EAEAEA;
  border-radius: 5.91537px;
}
.course-center-details .operation-pic .center-top .center-title .title3 .Progress-content .ant-progress-success-bg,
.course-center-details .operation-pic .center-top .center-title .title3 .Progress-content .ant-progress-bg {
  background: linear-gradient(90deg, #1BDDEC 0%, #07BDF9 100%);
  box-shadow: 0px 4px 8px rgba(37, 136, 191, 0.34);
  border-radius: 5.73466px;
}
.course-center-details .operation-pic .center-top .center-title .center-btn {
  text-align: right;
  position: absolute;
  right: 25px;
  bottom: 0;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .ant-btn {
  /*width: 56px;
                        height: 17px;
                        font-size: 14px;*/
  color: #2A283D;
  background: #3BB9FF;
  border-radius: 5px;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .ant-btn span {
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  color: #FFFFFF;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .btn-right {
  display: inline-block;
  margin-left: 15px;
  vertical-align: middle;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .btn-right img {
  box-sizing: border-box;
  display: inline-block;
  margin: -5px 5px 0;
}
.course-center-details .operation-pic .center-top .center-title .center-btn .btn-right .right-p {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.99;
  display: inline-block;
}
.course-center-details .operation-pic .center-content {
  margin: 30px 30px 30px 54px;
}
.course-center-details .operation-pic .center-content .center-content-left {
  padding-right: 27px;
}
.course-center-details .operation-pic .center-content .center-content-left .no-content {
  min-height: 400px;
}
.course-center-details .operation-pic .center-content .center-content-left .no-content .ant-empty {
  position: absolute;
  left: 40%;
  top: 30%;
  transform: translate(-30%, -50%);
}
.course-center-details .operation-pic .center-content .center-content-left .ant-tabs {
  margin: 10px 15px 30px 30px;
}
.course-center-details .operation-pic .center-content .center-content-left .ant-tabs .ant-tabs-bar {
  margin: 0 0 30px 0;
}
.course-center-details .operation-pic .center-content .center-content-left .ant-tabs-tab {
  color: #2A283D;
  font-size: 18px;
  opacity: 0.5;
}
.course-center-details .operation-pic .center-content .center-content-left .ant-tabs-tab-active {
  opacity: 1 !important;
}
.course-center-details .operation-pic .center-content .center-content-left .tab3 {
  min-height: 400px;
}
.course-center-details .operation-pic .center-content .center-content-left .tab3 .center {
  text-indent: 2em;
  color: #2A283D;
  height: 100%;
  line-height: 26px;
}
.course-center-details .operation-pic .center-content .fixed-center {
  position: fixed;
  right: 26px;
}
.course-center-details .operation-pic .center-content .fixed-center .no-content {
  min-height: 400px;
}
.course-center-details .operation-pic .center-content .fixed-center .no-content .ant-empty {
  position: absolute;
  left: 40%;
  top: 30%;
  transform: translate(-30%, -50%);
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right {
  margin: 0 18px 18px 8px;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right .center-content-right-title {
  font-size: 22px;
  line-height: 120.7%;
  color: #2A283D;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right .center-content-right-img {
  text-align: right;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right .center-content-right-img span {
  color: #00BBF8 !important;
  font-size: 14px;
  line-height: 120.7%;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right .center-content-right-img span img {
  margin-top: -2px;
  margin-right: 10px;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right-content {
  margin: 32px 0 10px 20px;
  cursor: pointer;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right-content .center-content-right-content-img {
  margin-right: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 75px;
  width: 94px;
  position: relative;
  background: url(../../../../assets/images/placeholder_bg.png) no-repeat;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right-content .center-content-right-content-img img {
  width: 100%;
  height: 100%;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right-content .center-content-right-content-img > span {
  padding: 2px 6px;
  position: absolute;
  left: 2px;
  bottom: 2px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 2px;
  color: #fff;
  font-size: 12px;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right-content .center-content-right-content-img > span .course-logo-l {
  width: 12px !important;
  height: 12px !important;
  vertical-align: -1px;
  margin-right: 5px;
  display: inline-block;
  min-width: auto;
  min-height: auto;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right-content .right-title1 {
  font-size: 18.0844px;
  line-height: 120.7%;
  color: #2A283D;
  margin-bottom: 7px;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right-content .right-title2 {
  color: #2A283D;
  font-size: 14px;
  opacity: 0.4;
  margin-bottom: 4px;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right-content .right-title2:last-child {
  margin-bottom: 0;
}
.course-center-details .operation-pic .center-content .fixed-center .center-content-right-content:hover .right-title1 {
  color: #3BB9FF;
}
html {
  overflow-x: auto;
  overflow-y: auto;
}
